.loader {
  height: 150px;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 15px;
  height: 15px;
  color: #000;
  transform-origin: 35px -35px;
  background: currentColor;
  border-radius: 50%;
  margin: 10px auto;
  animation: hitL .3s ease-in infinite alternate;
  display: block;
  position: relative;
  transform: rotate(45deg);
  box-shadow: 0 50px, 0 100px;
}

.loader:after {
  transform-origin: -35px -35px;
  animation: hitR .3s ease-out infinite alternate;
  left: 64px;
  transform: rotate(-45deg);
}

@keyframes flash {
  0%, 100% {
    background-color: rgba(255, 255, 255, .25);
    box-shadow: 32px 0 rgba(255, 255, 255, .25), -32px 0 rgba(255, 255, 255, .25);
  }

  25% {
    background-color: rgba(255, 255, 255, .25);
    box-shadow: 32px 0 rgba(255, 255, 255, .25), -32px 0 #fff;
  }

  50% {
    background-color: #fff;
    box-shadow: 32px 0 rgba(255, 255, 255, .25), -32px 0 rgba(255, 255, 255, .25);
  }

  75% {
    background-color: rgba(255, 255, 255, .25);
    box-shadow: 32px 0 #fff, -32px 0 rgba(255, 255, 255, .25);
  }
}

@keyframes hitL {
  0% {
    background-color: #fff;
    transform: rotate(45deg);
  }

  25%, 100% {
    background-color: rgba(255, 255, 255, .25);
    transform: rotate(0deg);
  }
}

@keyframes hitR {
  0%, 75% {
    background-color: rgba(255, 255, 255, .25);
    transform: rotate(0deg);
  }

  100% {
    background-color: #fff;
    transform: rotate(-45deg);
  }
}

